   while (searchTime !== 0) {\r\n                            activeName = el.dataset && el.dataset.initiator\r\n                            if (activeName == null) {\r\n                                el = el.parentNode\r\n                                searchTime -= 1\r\n                            } else {\r\n                                return\r\n                            }\r\n                        }\r\n                    }\r\n                    if (activeName == null) {\r\n                        this.$store.commit(SWITCH_NAME, null)\r\n                        return\r\n                    }\r\n                    if (activeName !== this.$store.getters['activeTool']) {\r\n                        this.$store.commit(SWITCH_NAME, activeName)\r\n                    }\r\n                }.bind(this))\r\n    },\r\n    methods: {\r\n        closePrompt() {\r\n            this.$store.commit('M_UPDATE_PROMPT', {\r\n                texts: '',\r\n                show: false,\r\n                type: ''\r\n            })\r\n        }\r\n    }\r\n}\r\n</script>\r\n<style type=\"text/css\">\r\n    .menu-container{\r\n        padding-top: 1px;\r\n        padding-left: 2px;\r\n    }\r\n</style>"],"sourceRoot":""}]);

